import axios from 'axios';
import { LocationConfiguration } from '../../models/location';

export async function getRestaurant(location: LocationConfiguration, query?: any) {
    const locationApi = process.env.REACT_APP_LOCATION_API;
    const categoriesParams = location.categories ? `&categories=${location.categories}` : '';
    const sortParams = location.sort ? `&sort=${location.sort}` : '';
    const limitParams = location.limit ? `&sort=${location.limit}` : '';
    const queryParams = query ? `&query=${query}` : '';

    const URL =
        locationApi +
        `places/search?ll=${location.geocode?.latitude},${location.geocode?.longitude}&radius=${location.radius}${categoriesParams}${sortParams}${limitParams}${queryParams}`;
    // console.log(URL);

    const headers = {
        Accept: 'application/json',
        Authorization: 'fsq3eoIQWoWU3cbi90ZnsWYXHTvD16TvLeM2ZipHJoASdbE=',
    };

    const results = await axios.get(URL, { headers });

    // console.log(results);
    if (results.status === 200) return results.data;
    return null;
}
